.ct-all-fonts {

	ul {
		li {
			justify-content: space-between;

			> span {
				display: flex;
				flex-direction: column;
				margin-right: auto;

				i {
					margin-top: 2px;
					font-size: 12px;
					color: rgba(85, 93, 102, 0.8);
				}
			}
		}

		button {
			display: flex;
			align-items: center;
			justify-content: center;
			position: relative;
			width: 25px;
			height: 25px;
			padding: 0;
			border: none;
			cursor: pointer;
			appearance: none;
			opacity: 0.7;
			transition: color 0.2s cubic-bezier(0.455, 0.03, 0.515, 0.955);

			&:hover {
				opacity: 1;

				.ct-tooltip-top {
					opacity: 1;
					visibility: visible;
					transform: translate3d(0px, -38px, 0px);
				}
			}

			&:last-of-type {
				margin-left: 7px;
			}

			&:before {
				font-family: dashicons;
				font-size: 17px;
			}
		}

		.ct-remove-font:before {
			content: "\f182";
		}

		.ct-edit-font:before {
			content: "\f111";
		}
	}

	ul,
	.ct-notification {
		margin: 20px 0;
		padding: 20px 0;
		border-top: 1px dashed #eee;
		border-bottom: 1px dashed #eee;
	}

	.ct-button-primary {
		--buttonHeight: 42px;
		--buttonPadding: 20px;
		--buttonFontSize: 14px;
		width: 100%;	
	}
}