@import 'all-fonts';
@import 'upload-font';

.ct-custom-fonts-settings {
	ul {
		li {
			display: flex;
			align-items: center;

			&:not(:last-child) {
				margin-bottom: 20px;
				padding-bottom: 20px;
				border-bottom: 1px dashed #eee;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	p code {
		background: rgba(0, 0, 0, 0.05);
	}
}