.ct-upload-fonts {

	p {
		margin-bottom: 20px;
		padding-bottom: 20px;
		border-bottom: 1px dashed #eee;
	}

	ul {
		margin: 20px 0 40px 0;
		padding-bottom: 20px;
		border-bottom: 1px dashed #eee;

		li {
			position: relative;

			.ct-button-primary {
				--buttonHeight: 30px;
			}

			.ct-font-preview {
				font-size: 12px;
				padding: 0 10px;
			}

			.ct-select-input {
				max-width: 150px;
				margin-left: auto;
			}

			.ct-remove {
				position: absolute;
				top: 2px;
				right: -28px;
				width: 24px;
				height: 24px;
				padding: 0;
				appearance: none;
				border: none;
				cursor: pointer;

				&:hover {
					color: #e05757;
				}

				&:focus {
					outline: none;
				}
			}
		}
	}
}

.ct-font-name-variation {
	display: grid;
	grid-template-columns: 1fr 150px;
	grid-column-gap: 10px;
	padding: 10px;
	margin: 0 -10px;
	border-radius: 3px;
	background: rgba(238, 238, 238, 0.5);

	input {
		--fontSize: 13px;
		--height: 35px;
	}
}

.ct-font-save {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-column-gap: 20px;

	[class*="ct-button"] {
		--buttonHeight: 42px;
		--buttonPadding: 20px;
		--buttonFontSize: 14px;
		width: 100%;

		// &:first-child {
		// 	margin-bottom: 10px;
		// }	
	}
}